body {
  margin: 0;
  padding: 0;
  background-image: url('../src/fondo1.jpg');
  background-size: cover; /* Ajusta según tus preferencias */
  background-position: center; /* Ajusta según tus preferencias */
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Monospace', monospace;
  color: #ffffff; /* Color del texto inspirado en Kali Linux */
}
.terminal-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.toolbar {
  position: absolute;
  top: 0;
  right: 0;
  padding: 8px;
}

.close-button {
  background-color: #ff4d4d; /* Color de fondo del botón cerrar */
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 5px;
}
.terminal {
  position: relative;
  padding: 20px;
  border: 2px solid #2f2f2f; /* Color del borde inspirado en Kali Linux */
  border-radius: 5px;
  background-color: #1a1a1a; /* Color del fondo inspirado en Kali Linux */
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5); /* Sombra inspirada en Kali Linux */
  white-space: pre-line;
  text-align: left;
  max-width: 80%;
  line-height: 1.4;
  transition: transform 0.2s; /* Transición al pasar el mouse */
}



.prompt {
  margin-top: 4%;
  color: #4caf50; /* Color del prompt inspirado en Kali Linux */
  display: block;
  margin-bottom: 8px;
}

.command {
  color: #ffffff; /* Color del comando inspirado en Kali Linux */
}

.comment {
  color: #757575; /* Color del comentario inspirado en Kali Linux */
  font-style: italic;
}